.container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
}

.text {
  composes: fancy from '../../styles/typography.css';
  width: 110px;
}

.val {
  composes: fancy from '../../styles/typography.css';
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;

  margin-right:5px;
  width: 50px;
  height: 30px;
  background: #FFCCD5;
  position: relative;
  border-radius:         3px;
}

.val:after {
  content:"";
  position: absolute;
  left: 100%;
  top: 11px;
  width: 0;
  height: 0;
  border-top: 4px solid transparent;
  border-left: 8px solid #FFCCD5;
  border-bottom: 4px solid transparent;
}

.slider {
  -ms-flex-positive: 1;
      flex-grow: 1;
  height: 24px;
  -webkit-appearance: none;
  margin: 10px 0;
  width: 100%;
}

.slider:focus {
  outline: none;
}

.slider::-webkit-slider-runnable-track {
  width: 100%;
  height: 2px;
  cursor: pointer;
  animate: 0.2s;
  -webkit-box-shadow: 0px 0px 1px #A6A6A6;
          box-shadow: 0px 0px 1px #A6A6A6;
  background: #E6D7D9;
  border-radius: 2px;
  border: 0px solid #FFD6DC;
}

.slider::-webkit-slider-thumb {
  -webkit-box-shadow: 1px 1px 1px #A6A6A6;
          box-shadow: 1px 1px 1px #A6A6A6;
  border: 2px solid #FFCCD5;
  height: 15px;
  width: 15px;
  border-radius: 0px;
  background: #FBE9E9;
  cursor: pointer;
  -webkit-appearance: none;
  margin-top: -7.5px;
}

.slider:focus::-webkit-slider-runnable-track {
  background: #E6D7D9;
}

.slider::-moz-range-track {
  width: 100%;
  height: 2px;
  cursor: pointer;
  animate: 0.2s;
  box-shadow: 0px 0px 1px #A6A6A6;
  background: #E6D7D9;
  border-radius: 2px;
  border: 0px solid #FFD6DC;
}

.slider::-moz-range-thumb {
  box-shadow: 1px 1px 1px #A6A6A6;
  border: 2px solid #FFCCD5;
  height: 15px;
  width: 15px;
  border-radius: 0px;
  background: #FBE9E9;
  cursor: pointer;
}

.slider::-ms-track {
  width: 100%;
  height: 2px;
  cursor: pointer;
  animate: 0.2s;
  background: transparent;
  border-color: transparent;
  color: transparent;
}

.slider::-ms-fill-lower {
  background: #E6D7D9;
  border: 0px solid #FFD6DC;
  border-radius: 4px;
  box-shadow: 0px 0px 1px #A6A6A6;
}

.slider::-ms-fill-upper {
  background: #E6D7D9;
  border: 0px solid #FFD6DC;
  border-radius: 4px;
  box-shadow: 0px 0px 1px #A6A6A6;
}

.slider::-ms-thumb {
  margin-top: 1px;
  box-shadow: 1px 1px 1px #A6A6A6;
  border: 2px solid #FFCCD5;
  height: 15px;
  width: 15px;
  border-radius: 0px;
  background: #FBE9E9;
  cursor: pointer;
}

.slider:focus::-ms-fill-lower {
  background: #E6D7D9;
}

.slider:focus::-ms-fill-upper {
  background: #E6D7D9;
}


